<template>
  <div class="send-message-container">
    
    <van-field
      v-model="message"
      rows="12"
      clearable
      type="textarea"
      maxlength="1000"
      placeholder="写留言和大家互动吧"
      show-word-limit
      :border="false"
    />

    <van-button
      @click="sendMessage"
      class="send-btn"
      color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"      text="发送"
      round
    />
    
  </div>
</template>

<script>
import Vue from 'vue';
import { Field, Button, Toast } from 'vant';
import { apiSaveLeaveMessage } from '@/service/api/leaveMessage';

Vue.use(Field).use(Button).use(Toast);

export default {
  data() {
    return {
      message: '',
      targetId: null,
      albumId: null,
      sectionId: null
    }
  },

  created() {
    document.title = '发送留言';
  },

  mounted() {
    this.receiveQuery();
  },

  methods: {
    // todo 保存 query 参数到组件中
    receiveQuery() {
      this.message = this.$route.query.message;
      this.albumId = this.$route.query.albumId;
      this.sectionId = this.$route.query.sectionId;
    },
    
    // todo 发送留言
    async sendMessage() {
      const saveMessageResponse = await apiSaveLeaveMessage({
        targetId: this.sectionId ? this.sectionId : this.albumId,
        type: this.sectionId ? 'section' : 'album',
        albumId: this.albumId,
        content: this.message
      });
      if (!saveMessageResponse && saveMessageResponse.type === 'fail') return Toast('服务器错误，请联系客服');

      Toast( '发送成功' );
      this.message = '';
      this.$router.back();
    }
  }
}
</script>

<style lang="scss" scoped>
.send-message-container {
  width: 100%;
  height: 100%;
  background: rgba(246,246,246,1);

  .send-btn {
    width: calc(100% - 40px);
    margin: 20px 20px 0;
    height: 40px;
  }
}
</style>


